import * as React from 'react';
import { ItemKeys, ItemSlot, SlotType, RenderToolbar } from '@ali/4ever-component';
import { FindAndReplaceButton, Iprops } from '../components/next/FindAndReplaceButton';
import { ControllerManager } from '../utils';

const buttons: Partial<Record<ItemKeys, React.ComponentType<Iprops>>> = {
  findAndReplace: FindAndReplaceButton,
};

export default function createRenderToolbar(
  locale: Record<string, string>,
  ctlManager: ControllerManager,
): RenderToolbar {
  return (props, controller) => {
    const { buttonName, extraProps } = props;

    const Component = buttons[buttonName] as React.ComponentType<Iprops>;
    if (Component) {
      return (
        <ItemSlot display={SlotType.iconButton}>
          <Component
            controller={controller}
            locale={locale}
            ctlManager={ctlManager}
            {...extraProps}
          />
        </ItemSlot>
      );
    }

    return null;
  };
}
